import styled from 'styled-components';

const StyledSection = styled.div`
  padding: 2rem;
`;

const StyledContainer = styled.div`
  max_width: ${props => props.theme.maxWidth};
  margin: 0 auto;
`;

const Section = props => {
  return (
    <StyledSection>
      {props.container ? (
        <StyledContainer>
          {props.children}
        </StyledContainer>
      ) : (
        <div>
          {props.children}
        </div>
      )}
    </StyledSection>
  );
}

export default Section;
